<template>
  <editorial v-if="currentExperiment" class="experiments">
    <template #subtitle>Experiments</template>
    <template #title>
      {{ currentExperiment.title }}
    </template>
    <p>{{ currentExperiment.description }}</p>
    <div
      v-if="currentExperiment.type === 'link'"
      class="content"
    >
      <a
        :href="currentExperiment.data"
        target="_blank"
        noreferrer
        noopener
        style="display: inline-flex; flex-direction: row"
      >
        Open experiment
        <icon name="open_in_new" size="s" />
      </a>
    </div>
    <div
      v-else-if="currentExperiment.type === 'jsfiddle'"
      class="content"
    />
    <template v-else-if="currentExperiment.type === 'component'">
      <component :is="currentExperiment.name" />
    </template>
  </editorial>
</template>

<script>
import experiments from '@/experiments';

const components = {};
experiments.forEach((experiment) => {
  if (experiment.type === 'component') {
    components[experiment.name] = () => import(`@/experiments/${experiment.name}`);
  }
});
console.warn(experiments, components);

export default {
  name: 'Experiment',
  components,
  props: {
    experiment: {
      type: String,
      default: null,
    },
  },
  data: () => ({
    experiments,
    currentExperiment: null,
  }),
  watch: {
    experiment: {
      handler(experiment) {
        if (experiment) {
          this.currentExperiment = experiments.find(x => x.name === experiment);
        } else {
          this.currentExperiment = null;
        }
      },
      immediate: true,
    },
  },
  // mounted() {
  //   experiments.forEach((exp, index) => {
  //     if (exp.type === 'jsfiddle') {
  //       const content = this.$refs.experiment[index].querySelector('.content');
  //       const script = document.createElement('script');
  //       script.async = true;
  //       script.src = exp.data;
  //       content.appendChild(script);
  //     }
  //   });
  // },
};
</script>

